.calendar-container {
  text-align: center;
  font-family: 'Cormorant Garamond', serif;
  color: #333;
  padding-top: 80px;
  padding-bottom: 80px;
  background-color: #efeeee;
}

.calendar-title {
  font-size: 40px;
  margin-bottom: 25px;
  font-weight: 300;
  letter-spacing: 2px;
}

.calendar-month {
  font-size: 26px;
  margin-bottom: 15px;
  text-transform: uppercase;
  font-weight: 500;
}

.calendar {
  margin: 0 auto;
  border-collapse: collapse;
  font-size: 20px;
}

.calendar th,
.calendar td {
  width: 80px;   /* ширина ячейки */
  height: 80px;  /* высота ячейки */
  text-align: center;
  vertical-align: middle;
  position: relative;
}

.calendar th {
  font-weight: 600;
  font-size: 18px;
  padding-bottom: 5px;
}

.red {
  color: #d43c3c;
}

.highlight {
  text-align: center;
}

/* Контейнер для кольца и числа */
.ring-wrapper {
  position: relative;
  width: 64px;   /* размер кольца */
  height: 64px;
  margin: 0 auto; /* по центру в ячейке */
}

.ring-wrapper img {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: contain;
  transform: translate(8px, -8px) rotate(45deg);
}

.ring-wrapper span {
  position: absolute;
  inset: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 22px;
  font-weight: bold;
}

.calendar-date {
  font-size: 26px;
  margin-top: 25px;
  letter-spacing: 2px;
}
